.login {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 400px;
    width: 80%;
    border: 1.5px solid var(--secondary-color);
    padding: 2rem 1.5rem;
    box-sizing: border-box;
    border-radius: 10px;
    text-align: center;
}

.login .header {
    padding: 2rem 0;
    text-align: center;
}

.login a {
    color: black;
    font-size: var(--body-size);
    text-decoration: none;
}

p.alert {
    background-color: #ff2a7c;
    color: white;
    padding: 10px 20px;
    box-sizing: border-box;
    border-radius: 10px;
    margin-top: 1rem;
    font-size: var(--body-size);
}

.login .header h1 {
    font-size: var(--title-size);
}

.login .input {
    width: 100%;
    margin-bottom: 20px;
}

.login .input input {
    border-radius: 10px;
    outline: none;
    width: 100%;
    background-color: white;
    padding: 10px 20px;
    box-sizing: border-box;
    font-size: var(--body-size);
    border: 1.5px solid var(--secondary-color);
}

.login button {
    width: 100%;
    padding: 20px 20px;
    box-sizing: border-box;
    font-size: var(--body-size);
    border: none;
    outline: none;
    background-color: var(--primary-color);
    color: white;
    border-radius: 10px;
    font-weight: 600;
    margin-bottom: 20px;
}