p {
    font-size: var(--body-size);
}

hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid var(--secondary-color);
    margin: 1em 0;
    padding: 0;
}

.alert_success {
    padding: 15px 20px;
    background-color: rgb(0, 196, 102);
    border-radius: 10px;
    color: white;
    font-size: var(--body-size);
}

.container {
    display: flex;
    column-gap: 20px;
    margin-top: 2rem;
}

.container .input {
    width: 50%;
}

.container .input textarea {
    width: 100%;
    border: none;
    outline: none;
    border: 1.5px solid var(--secondary-color);
    resize: none;
    padding: 15px;
    box-sizing: border-box;
    font-size: var(--body-size);
    border-radius: 10px;
}

.container .input button {
    width: 100%;
    padding: 15px;
    box-sizing: border-box;
    background-color: white;
    border: none;
    border: 1.5px solid var(--secondary-color);
    font-size: var(--body-size);
    font-weight: 600;
    border-radius: 5px;
    margin-top: 10px;
    cursor: pointer;
    border-radius: 10px;
}

.container .preview {
    width: 50%;
    border: 1.5px solid var(--secondary-color);
    padding: 15px;
    box-sizing: border-box;
    overflow-y: scroll;
    height: 500px;
    border-radius: 10px;
}

@media (max-width: 768px) {
    .container {
        flex-direction: column;
        row-gap: 50px;
    }

    .container .input {
        width: 100%;
    }

    .container .preview {
        width: 100%;
    }
}