p {
    font-size: var(--body-size);
}

hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid var(--secondary-color);
    margin: 1em 0;
    padding: 0;
}

.container {
    display: flex;
    justify-content: space-between;
    column-gap: 20px;
}

@keyframes transitionBottomUp {
    0%{
        transform: translateY(100%);
    }
    100% {
        transform: translateY(0);
    }
}

.main {
    width: 60%;
    margin-top: 2rem;
    padding-bottom: 2rem;
}

.main .option {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
}

.main .option button {
    width: 100%;
    border: none;
    outline: none;
    background-color: white;
    border: 1.5px solid var(--secondary-color);
    padding: 10px 20px;
    box-sizing: border-box;
    font-size: var(--body-size);
    cursor: pointer;
}

.main .info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 15px 15px 15px;
    box-sizing: border-box;
    border: 1.5px solid var(--secondary-color);
}

.main .info p {
    font-size: calc(var(--body-size) - 3px);
}

.main .content {
    display: flex;
    flex-direction: column;
    row-gap: 15px;
    border: 1.5px solid var(--secondary-color);
    padding: 1rem;
    box-sizing: border-box;
    border-radius: 5px;
    animation: transitionBottomUp .3s;
}

.main .navigation {
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 5px;
    padding: 10px 0;
}

.main .navigation button {
    width: 100%;
    border: none;
    outline: none;
    background-color: white;
    border: 1.5px solid var(--secondary-color);
    padding: 10px 20px;
    box-sizing: border-box;
    font-size: var(--body-size);
    cursor: pointer;
    border-radius: 10px;
}

.side {
    width: 40%;
}

.side .subject {
    position: sticky;
    top: 102px;
    z-index: 99;
}

.side .subject button {
    width: 100%;
    padding: 15px;
    box-sizing: border-box;
    border: none;
    background-color: var(--secondary-color);
    font-size: var(--body-size);
    font-weight: 600;
    border-radius: 5px;
    margin-top: 10px;
    cursor: pointer;
    border-radius: 10px;
}

.side .subject button:hover {
    color: white;
    background-color: var(--third-color);
}

.side .subject button.create {
    background-color: white;
    border: 1.5px solid var(--secondary-color);
    margin-bottom: 20px;
    border-radius: 10px;
    margin-top: 2rem;
}

.side .subject button:hover {
    color: white;
    background-color: var(--primary-color);
}

@media (max-width: 768px) {
    .container {
        flex-direction: column;
    }

    .main {
        width: 100%;
        padding-bottom: 0;
    }

    .side {
        width: 100%;
    }

    .side .subject {
        position: unset;
        padding: 2rem 0;
    }
}