.list {
    border: 1px solid var(--secondary-color);
    padding: 15px;
    border-radius: 5px;
    box-sizing: border-box;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.list p {
    font-size: var(--body-size);
}

.list:hover {
    color: white;
    background-color: var(--primary-color);
}

.list .option {
    position: absolute;
    display: flex;
    
}