.container {
    display: flex;
    justify-content: space-between;
    column-gap: 20px;
}

@keyframes transitionFade {
    0%{
        opacity: 0.1;
    }
    100% {
        opacity: 1;
    }
}

.main {
    width: 60%;
    padding: 2rem 0;
    display: flex;
    flex-direction: column;
    row-gap: 15px;
    animation: transitionFade .3s;
}

.main .banner {
    width: 100%;
    background-color: var(--primary-color);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 20px;
    box-sizing: border-box;
    border-radius: 10px;
}

.main .banner img {
    width: 50%;
}

.main .banner h2 {
    font-size: var(--body-size);
    color: white;
}

.main .subject {
    margin-top: 1.5rem;
    padding-bottom: 2rem;
}

.main .subject .input {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 10px;
}

.main .subject .input input {
    width: 100%;
    border: none;
    outline: none;
    background-color: white;
    font-size: var(--body-size);
    border: 1.5px solid var(--secondary-color);
    border-radius: 10px;
    padding: 15px 20px;
    box-sizing: border-box;
}

.main .subject .input p {
    font-size: 25px;
    cursor: pointer;
    font-weight: 800;
}

.main .subject button {
    width: 100%;
    padding: 15px;
    box-sizing: border-box;
    border: none;
    font-size: var(--body-size);
    font-weight: 600;
    border-radius: 5px;
    margin-top: 10px;
    cursor: pointer;
    background-color: white;
    border: 1.5px solid var(--secondary-color);
    border-radius: 10px;
}

.main .subject button:hover {
    color: white;
    background-color: var(--primary-color);
}

.side {
    width: 40%;
    height: 500px;
    padding: 2rem 0;
}

.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    max-width: 400px;
    width: 90%;
    padding: 2rem 1rem;
    box-sizing: border-box;
    border-radius: 10px;
}

.modal p {
    float: right;
    font-size: 25px;
    color: var(--primary-color);
    font-weight: 800;
    cursor: pointer;
}

.modal .input input {
    outline: none;
    border: 1.5px solid var(--secondary-color);
    width: 100%;
    padding: 10px 20px;
    border-radius: 10px;
    font-size: var(--body-size); 
    box-sizing: border-box;   
}

.modal .option {
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 10px;
    margin-top: 20px;
}

.modal .option button {
    width: 100%;
    border: none;
    outline: none;
    padding: 10px 20px;
    box-sizing: border-box;
    border-radius: 10px;
    font-size: var(--body-size);
    cursor: pointer;
}

.modal .option button:nth-child(1) {
    background-color: var(--primary-color);
    color: white;
}

.modal .option button:nth-child(2) {
    background-color: white;
    border: 1.5px solid var(--secondary-color);
}

@media (max-width: 600px) {
    .side {
        display: none;
    }

    .container {
        flex-direction: column;
    }
    
    .main {
        width: 100%;
    }
    
}